@value colorPink from "../../common/color.css";

/* tablet(768~1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
  .navbar {
    font-family: "Montserrat", "Nanum Gothic", -apple-system, BlinkMacSystemFont,
      "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
      "Droid Sans", "Helvetica Neue", sans-serif;
    background-color: #f7f9fa;
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }
}
/* mobile(~768px) */
@media (max-width: 768px) {
  .navbar {
    font-family: "Montserrat", "Nanum Gothic", -apple-system, BlinkMacSystemFont,
      "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
      "Droid Sans", "Helvetica Neue", sans-serif;
    background-color: #f7f9fa;
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

@media (min-width: 1025px) {
  .navbar {
    font-family: "Montserrat", "Nanum Gothic", -apple-system, BlinkMacSystemFont,
      "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
      "Droid Sans", "Helvetica Neue", sans-serif;
    background-color: #f7f9fa;
    width: 4.375rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 8vh;
    left: 0;
    bottom: 0;
  }
}

.item {
  width: 100%;
  font-weight: 600;
  color: #6a6d72;
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  justify-content: center;
  align-items: center;
}
.item_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  padding: 10px;
  border-radius: 5px;
}
.item_content:hover {
  background-color: #eceeef;
}
.item_icon {
  font-size: 17px;
  color: colorPink;
}
.item_text {
  margin-top: 10px;
  font-size: 12px;
}
